import React, { useState, useEffect, Component } from 'react';
import bridge from '@vkontakte/vk-bridge';
import View from '@vkontakte/vkui/dist/components/View/View';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import '@vkontakte/vkui/dist/vkui.css';
import Alert from '@vkontakte/vkui/dist/components/Alert/Alert';

import Home from './panels/Home';
import Persik from './panels/Persik';
import Rating from './panels/Rating';
import { render } from 'react-dom';
import { Switch } from '@vkontakte/vkui';


	




class App extends React.Component {
	constructor(props) {
	  super(props);
	  this.state = {
		activePanel: 'rating',
		fetchedUser: null,
		popout: null,
	  };
	  this.closePopout = this.closePopout.bind(this);
	}
	componentDidMount() {
	  bridge.subscribe((e) => {
		switch (e.detail.type) {
		  case 'VKWebAppGetUserInfoResult':
			this.setState({ fetchedUser: e.detail.data, activePanel: 'home' });
			break;
		  case 'VKWebAppShowWallPostBoxResult':
			this.openPopout()
			break;
		  default:
			console.log(e.detail.type);
		}
	  });
	  bridge.send('VKWebAppGetUserInfo', {});
	}
	go = (e) => {
	  this.setState({ activePanel: e.currentTarget.dataset.to })
	};
	share = () => {
	  bridge.send("VKWebAppShowWallPostBox", {"message": "Я создаю свой сервис!", "attachments": "article102343170_20574_329070dc1572eed67b"});
	}
	openPopout = () => {
	  this.setState({ popout:
		<Alert
		  actions={[{
			title: 'Ок',
			autoclose: true,
			style: 'destructive'
		  }]}
		  onClose={this.closePopout}
		>
		  <h2>Спасибо!</h2>
		  <p>Мы рады, что вы сделали репост!</p>
		</Alert>
	  });
	}
	closePopout = () => {
	  this.setState({ popout: null });
	}
	render() {
	  return (
		<View popout={this.state.popout} activePanel={this.state.activePanel}>
		  <Home id="home" share={this.share} fetchedUser={this.state.fetchedUser} go={this.go} />
		  <Persik id="persik" go={this.go} />
		  <Rating id="rating" go={this.go} />
		</View>
	  );
	}
  }
  export default App;